<template>
  <div>
    <top/>
    <!--<topMember :index="2"></topMember>-->
    <div class="box mar fffbg" style="min-height:500px;">
      <div class="mainH">
        <div class="mainHICO"></div>
        <span class="pull-left">第一步：选择数据来源平台</span>
      </div>
      <ul class="firstType">
        <li v-for="item in topData">
          <div class="imgBox">
            <img :src="item.img"/>
            </div>
            <p class="typeText">{{item.name}}</p>
        </li>
      </ul>
    </div>
    <foot></foot>


  </div>
</template>

<script>
  import foot from "./public/foot.vue";
  import top from "./public/top.vue";
  export default {
    components: {
      foot,top,
    },
    data() {
      return {
        type:'1',
        tableData:[{work:'aaa',name:'bbb',
          submitTime:'ccc',endTime:'ddd'}],
        topData:[{img:require('../assets/images/taobao.png'),name:'淘宝'},
          {img:require('../assets/images/tmall.png'),name:'天猫'},
          {img:require('../assets/images/jd.png'),name:'京东'},
          {img:require('../assets/images/pdd.png'),name:'拼多多'},
          {img:require('../assets/images/other.png'),name:'其他'}],
      };
    },
    mounted() {

      this.getList();
    },
    methods: {
      getList() {
        let that = this;
        that.list = [];
        this.axios
          .get(
            // process.env.API_HOST+"goods?page="+(that.page-1)+"&size="+that.size,
            "/giftmall/goods?state=0&page=" +
            (that.page - 1) +
            "&size=" +
            that.size,
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded"
              }
            }
          )
          .then(function(response) {
            if (response.data.code == 200) {
              that.list = response.data.data.list;
              that.total = response.data.data.totalCount;
              for (var i = 0; i < that.list.length; i++) {
                if (that.list[i].images == null) {
                  that.list[i].images = "../../static/nopic.jpg";
                }
              }
            }
          });
      },
    }
  };
</script>
<style lang="less">
  .el-table .warning-row {
    background: #fdebec;
  }
</style>
<style scoped lang="less">
  .tip{
    margin: 0px 1%;
    font-size: 12px;
    line-height: 20px;
    color:gray;
  }
  .firstType{
    background: #fff;
    padding: 30px 20px;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    margin-bottom: 20px;
    li:hover{
      -webkit-box-shadow: 0px 0px 5px #CF9047;
      box-shadow: 0px 0px 5px #CF9047;
    }
    li{
      width: 90px;
      height: 90px;
      cursor: pointer;
      margin-right: 25px;
      border: 1px solid #e9e9e9;
      border-radius: 4px;
      -webkit-box-shadow: 5px 5px 5px #eff2f7;
      box-shadow: 5px 5px 5px #eff2f7;
      p{
        padding: 0;
        margin: 0;
      }
      .imgBox {
        justify-content: center;
        display: flex;
        padding: 13px 0 6px;
      }
      .typeText {
        color: #1e1e1e;
        font-size: 14px;
        text-align: center;
      }
    }
  }
</style>



